.header{max-width:1200px;margin:auto;font-family:'Roboto Condensed', sans-serif;text-align:center;color:#fff}.header__wrap{padding:50px 10px;background:linear-gradient(135deg, #005585, #FF4D61, #4DBEFF, #00A2FF, #265F80);background-size:500% 100%;-webkit-animation:Gradient 30s ease infinite;animation:Gradient 30s ease infinite}.header__nav{display:flex;justify-content:flex-end;margin-bottom:60px}.header__info{display:flex;flex-direction:column;align-items:center}.header__title{margin:20px 0;padding:0;font-size:50px;line-height:68px;font-weight:bold}.header__desc{margin:0;margin-bottom:50px;padding:0;font-size:30px;line-height:32px;font-weight:normal}.nav__link{position:relative;margin-right:15px;font-family:'Roboto Condensed', sans-serif;font-size:24px;line-height:30px;color:#fff;text-decoration:none;text-transform:uppercase;-webkit-transition-duration:0.3s;transition-duration:0.3s;-webkit-transition-property:left, width, color;transition-property:left, width, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.nav__link:last-child{margin-right:0}.nav__link::before{content:'';position:absolute;left:50%;bottom:0;width:0;height:2px;background:#fff;-webkit-transition-duration:0.3s;transition-duration:0.3s;-webkit-transition-property:left, width, color;transition-property:left, width, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.nav__link:hover,.nav__link:focus{color:#0f1c20;-webkit-transition-duration:.8s;transition-duration:.8s;-webkit-transition-property:color;transition-property:color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.nav__link:hover::before,.nav__link:focus::before{left:0;width:100%;background:#0f1c20;-webkit-transition-duration:0.5s;transition-duration:0.5s;-webkit-transition-property:left, width, color;transition-property:left, width, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}@-webkit-keyframes Gradient{0%{background-position:0% 