.footer{max-width:1200px;margin:auto;padding:50px}.footer__wrap{background:linear-gradient(135deg, #005585, #FF4D61, #4DBEFF, #00A2FF, #265F80);background-size:500% 100%;-webkit-animation:Gradient 30s ease infinite;animation:Gradient 30s ease infinite}.footer__social{display:flex;justify-content:center;margin-bottom:40px}.footer__year{margin:20px 0;font-size:24px;line-height:32px;text-align:right}.footer__nav{text-align:right}@-webkit-keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

body{margin:0;padding:0;font-family:'Roboto Condensed', sans-serif}.hidden{display:none}

