.about__wrap{display:flex;flex-direction:column;max-width:1200px;margin:auto;padding:40px 0}.about__btn{display:flex;justify-content:space-between;align-items:center;width:100%;padding:0 30px;background-color:#fff;border:1px solid #ddd;-webkit-transition-duration:.5s;transition-duration:.5s;-webkit-transition-property:background-color, color, border;transition-property:background-color, color, border;-webkit-transition-timing-function:ease;transition-timing-function:ease}.about__btn:hover,.about__btn:focus{color:#fff;background-color:#0f1c20;border:1px solid #000;-webkit-transition-duration:.5s;transition-duration:.5s;-webkit-transition-property:background-color, color,border;transition-property:background-color, color,border;-webkit-transition-timing-function:ease;transition-timing-function:ease;outline:none}.about__btn:hover .about__arrow,.about__btn:focus .about__arrow{fill:#fff}.about__btn--wrap{color:#0f1c20}.about__title{margin:0;padding:20px 0;font-size:30px;line-height:40px;text-align:left}.about__arrow{display:block;width:20px;height:20px;background-image:url(../../static/media/arrow.2aea532b.svg);background-position:left 0 top 0;background-repeat:no-repeat;-webkit-transform:rotate(180deg);transform:rotate(180deg)}

.table{margin:30px 15px}.table__row{border-bottom:1px solid #f99}.table__td{font-size:16px;line-height:20px;font-weight:normal;border-bottom:1px solid #999}.table__td:hover,.table__td:focus{background-color:#b2e3ff}.bold{font-weight:bold;background-color:#b2e3ff}.bold:hover,.bold:focus{background-color:#4DBEFF}@media (max-width: 400px){.table__td{font-size:14px;line-height:20px;font-weight:normal;word-break:break-all}}

